.ingredients {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    gap: 16px;
    max-height: 400px;
    height: 100%;
}
.wrapper {
    display: flex;
    flex-direction: column;
    gap: 16px
}
.total {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.totalPrice {
    display: flex;
    align-items: center;
}
.total svg {
    width: 33px;
    height: 33px;
    margin-left: 9px;
}
.ingredients::-webkit-scrollbar {
    width: 8px;
}
.ingredients::-webkit-scrollbar-track {
    background-color:  #2F2F37;
}
.ingredients::-webkit-scrollbar-thumb {
    background-color: #8585AD;

}

.burgerConstructor {
    height: 75vh;
    display: flex;
    min-width: calc(30% - 20px);
    flex-flow: column;
    justify-content: space-between;
}

